  <div>
    <template v-if="authenticationView == 'sign-in'">
      <signIn />
    </template>
    <template v-if="authenticationView == 'sign-up'">
      <signUp />
    </template>
    <template v-if="authenticationView == 'forgot-password'">
      <forgotPassword />
    </template>
    <template v-if="authenticationView == 'verify-sign-up'">
      <verifySignUp />
    </template>
    <template v-if="authenticationView == 'verify-password'">
      <verifyPassword />
    </template>
  </div>
</template>

<script>
export default {
  components: {
    signIn: () => import("./components/sign-in"),
    signUp: () => import("./components/sign-up"),
    forgotPassword: () => import("./components/forgot-password"),
    verifySignUp: () => import("./components/verify-sign-up"),
    verifyPassword: () => import("./components/verify-password"),
  },
  computed: {
